import { memo } from "react";
import {
  Button,
  Acontainer,
  ArticleHeader,
  H1,
  ArticleBtns,
  ArticlesList
} from "../styled-components/styles";
import AddForm from "./AddForm";
import ArticleCard from "./ArticleCard";
import EditForm from "./EditForm";
import ViewForm from "./ViewForm";
function ArticleList({
  currentItems,
  data,
  toggleBigCards,
  deleteArticle,
  addArticle,
  handleView,
  view,
  aId,
  changeColor,
  handleEdit,
  handleChangeArticle,
  edit,
  showMore,
  curPage,
  lastPageIndex
}) {
  let list = currentItems.map((item, index) => (
    <ArticleCard
      id={item.id}
      data={item}
      key={index}
      ui={data.ui.makeBig}
      deleteArticle={deleteArticle}
      view={view}
      changeColor={changeColor}
      handleEdit={handleEdit}
    />
  ));
  const addForm = (e) => {
    const formId = e.target.id.slice(0, -4);
    const form = document.querySelector("#" + formId);

    if (form.parentNode.style.display === "none") {
      form.parentNode.style.display = "block";
    } else {
      form.parentNode.style.display = "none";
    }
  };
  const closeForm = (e) => {
    e.target.parentNode.parentNode.style.display = "none";
  };
  return (
    <>
      <Acontainer>
        <ArticleHeader>
          <H1>Article list</H1>
          <ArticleBtns>
            <Button onClick={toggleBigCards}>Make Big Cards</Button>
            <Button id="addForm-btn" onClick={addForm}>
              Add Article
            </Button>
          </ArticleBtns>
        </ArticleHeader>
        <ArticlesList>{list}</ArticlesList>
        <Button
          onClick={showMore}
          disabled={curPage === lastPageIndex ? "disabled" : ""}
        >
          Show more
        </Button>
      </Acontainer>
      <AddForm addArticle={addArticle} closeForm={closeForm} />
      <ViewForm data={aId} handleView={handleView} />
      <EditForm
        handleChangeArticle={handleChangeArticle}
        edit={edit}
        data={aId}
      />
    </>
  );
}
export default memo(ArticleList);
