import axios from "axios";
import { createRoot } from "react-dom/client";
import { Provider } from "react-redux";

import App from "./App";
import { addArticle } from "./Articles/actions";
import configStore from "./Articles/store/configStore";

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);
const store = configStore();
//const u = store.subscribe(() => console.log(store.getState()));
axios
  .get("https://jsonplaceholder.typicode.com/posts")
  .then((res) =>
    res.data.map((item) =>
      store.dispatch(addArticle(+item.id, +item.userId, item.title, item.body))
    )
  )
  .catch((err) => console.log("error", err));
root.render(
  <Provider store={store}>
    <App />
  </Provider>
);
