import styled from "styled-components";
export const DF = styled("div")`
  display: flex;
`;
export const RowDF = styled("div")`
  display: flex;
  flex-direction: row;
`;
export const ColDF = styled("div")`
  display: flex;
  flex-direction: column;
`;
export const H1 = styled("h1")`
  font-size: 30px;
`;
export const Button = styled("button")`
  background-color: rgb(127, 169, 238);
  border-radius: 5px;
  padding: 10px;
  color: #fff;
  cursor: pointer;
  outline: none;
  border: none;
  transition: 0.2s;
  :first-child {
    margin-right: 10px;
  }
  :disabled {
    opacity: 0.3;
  }
  :active {
    background-color: rgb(41 105 209);
  }
`;
export const Container = styled(RowDF)`
  max-width: 1200px;
  margin: 0 auto;
  height: 100%;
  align-items: center;
`;
export const Acontainer = styled(Container)`
  flex-direction: column;
  align-items: unset;
`;
export const Links = styled("a")`
  text-decoration: none;
  color: rgb(18 69 255);
  font-size: 22px;
  :first-child {
    margin-right: 10px;
  }
  :hover {
    text-decoration: underline;
    color: rgba(18, 69, 255, 0.3);
  }
`;
export const Nav = styled("nav")`
  background-color: rgb(204, 195, 195);
  padding: 20px 0;
`;
export const ArticleHeader = styled(RowDF)`
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
`;
export const ArticleBtns = styled(RowDF)`
  align-items: center;
`;
export const ArticlesList = styled(DF)`
  flex-wrap: wrap;
  justify-content: space-between;
`;
export const Articlecard = styled(ColDF)`
  width: 30%;
  margin-bottom: 30px;
  background-color: rgb(200, 198, 198);
  padding: 30px 15px;
  position: relative;
  transition: 1s ease;
  /* transition: width 1s ease; */
`;
export const ArticlecardBtn = styled("button")`
  padding: 10px;
  border: 0;
  outline: 0;
  border: 1px solid #000;
  cursor: pointer;
  :active {
    opacity: 0.3;
  }
`;
export const ArticlecardBtnDelete = styled(ArticlecardBtn)`
  position: absolute;
  top: 10px;
  right: 10px;
`;
export const ArticleTitle = styled("div")`
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 15px;
`;
export const ArticleBody = styled("div")`
  font-size: 18px;
  font-weight: 200;
  margin-bottom: 20px;
`;
export const FormWrap = styled("div")`
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
`;
export const Form = styled("form")`
  max-width: 500px;
  margin: 0 auto;
  margin-top: 10%;
  padding: 20px 30px;
  background-color: #fff;
  position: relative;
`;
export const FormTitle = styled("h4")`
  text-align: center;
  font-size: 18px;
`;
export const Close = styled("div")`
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
`;
