import { memo } from "react";
import {
  ArticleBody,
  Articlecard,
  ArticlecardBtn,
  ArticlecardBtnDelete,
  ArticleTitle,
  DF
} from "../styled-components/styles";
function ArticleCard({
  id,
  data,
  ui,
  deleteArticle,
  view,
  changeColor,
  handleEdit
}) {
  let title =
    data.title.length > 15 ? data.title.slice(0, 15) + "..." : data.title;
  let body = data.body.length > 50 ? data.body.slice(0, 50) + "..." : data.body;
  let style = {
    width: "",
    backgroundColor: ""
  };
  if (ui) {
    style.width = "45%";
  }
  if (data.changeColor === 1) {
    style.backgroundColor = "#c8c6c6";
  } else if (data.changeColor === 2) {
    style.backgroundColor = "#c1e2cf";
  } else if (data.changeColor === 3) {
    style.backgroundColor = "#b650f1";
  }
  return (
    <Articlecard id={id} style={style}>
      <ArticlecardBtnDelete onClick={deleteArticle}>
        Delete
      </ArticlecardBtnDelete>
      <ArticleTitle>{title}</ArticleTitle>
      <ArticleBody className="article-body">{body}</ArticleBody>
      <DF>
        <ArticlecardBtn
          id="viewForm-btn"
          onClick={(e) => {
            view(e);
          }}
        >
          View
        </ArticlecardBtn>
        <ArticlecardBtn onClick={changeColor}>Change Color</ArticlecardBtn>
        <ArticlecardBtn id="editForm-btn" onClick={handleEdit}>
          Edit
        </ArticlecardBtn>
      </DF>
    </Articlecard>
  );
}
export default memo(ArticleCard);
