import { memo, useEffect, useState } from "react";
import { FormWrap, Form, FormTitle, Close } from "../styled-components/styles";
function EditForm({ handleChangeArticle, edit, data }) {
  const [title, setTitle] = useState();
  const [body, setBody] = useState();
  useEffect(() => {
    if (data) {
      setTitle(data.title);
      setBody(data.body);
    }
  }, [data]);
  const close = (e) => {
    edit(data.id);
    e.target.parentNode.parentNode.style.display = "none";
  };
  const change = (e) => {
    if (e.target.name === "title") {
      setTitle(e.target.value);
    } else {
      setBody(e.target.value);
    }
  };
  return (
    <FormWrap id="editForm" style={{ display: "none" }}>
      <Form>
        <Close onClick={close}>X</Close>
        <FormTitle>Форма редактирования</FormTitle>
        {data ? (
          <div className="form-body">
            <input type="text" name="title" value={title} onChange={change} />
            <input type="text" name="body" value={body} onChange={change} />
            <input
              type="submit"
              value="Обновить"
              onClick={(e) =>
                handleChangeArticle(e, data.id, data.title, data.body)
              }
            />
          </div>
        ) : (
          ""
        )}
      </Form>
    </FormWrap>
  );
}
export default memo(EditForm);
