import { memo } from "react";
import { Link } from "react-router-dom";
import { Container, Links, Nav } from "../Articles/styled-components/styles";

function Header() {
  return (
    <Nav>
      <Container>
        <Links as={Link} to="/">
          Articles
        </Links>
        <Links as={Link} to="/users">
          Users
        </Links>
      </Container>
    </Nav>
  );
}
export default memo(Header);
