import { connect } from "react-redux";
import ArticleList from "../components/ArticleList";
import {
  addArticle,
  deleteArticle,
  toggleBigCards,
  handleChangeColor,
  handleChangeArticle,
  handleView,
  handleEdit
} from "../actions/";
import { useState } from "react";
let articleData;
function Articles(props) {
  const returnData = () => {
    return props.state;
  };
  const toggleBigCards = () => {
    props.toggleBigCards();
  };
  const deleteArticle = (e) => {
    props.deleteArticle(e.target.parentNode.id);
  };
  const addArticle = (e) => {
    e.preventDefault();
    let id = props.state.articles.length + 1;
    let userId = Math.floor(Math.random() * 100);
    let title = e.target.form[0].value;
    let body = e.target.form[1].value;
    let c = 0;
    for (let i = 0; i < e.target.form.length; i++) {
      if (e.target.form[i].type !== "submit") {
        if (e.target.form[i].value === "") {
          return false;
        } else {
          c += 1;
        }
      }
    }
    if (c === 2) {
      props.addArticle(id, userId, title, body);
      e.target.parentNode.parentNode.parentNode.style.display = "none";
      for (let i = 0; i < e.target.form.length; i++) {
        if (e.target.form[i].type !== "submit") {
          e.target.form[i].value = "";
        }
      }
    }
  };
  const handleView = (aId) => {
    props.handleView(aId);
  };
  const view = (e) => {
    let aId = e.target.parentNode.parentNode.id;
    handleView(aId);
    let id = e.target.id.slice(0, -4);
    let form = document.getElementById(id);
    props.state.articles.forEach((item) => {
      if (item.view === true) {
        articleData = item;
      }
    });
    if (form.style.display === "none") {
      form.style.display = "block";
    } else {
      form.style.display = "none";
    }
  };
  const changeColor = (e) => {
    let id = e.target.parentNode.parentNode.id;
    props.handleChangeColor(id);
  };
  const edit = (id) => {
    props.handleEdit(id);
  };
  const handleEdit = (e) => {
    let id = e.target.parentNode.parentNode.id;
    let form = e.target.id.slice(0, -4);
    form = document.getElementById(form);
    edit(id);
    props.state.articles.forEach((item) => {
      if (item.edit === true) {
        articleData = item;
      }
    });
    if (form.style.display === "none") {
      form.style.display = "block";
    } else {
      form.style.display = "none";
    }
  };
  const handleChangeArticle = (e, id, title, body) => {
    e.preventDefault();
    let newTitle = e.target.form[0].value;
    let newBody = e.target.form[1].value;
    props.handleChangeArticle(id, newTitle, newBody);
    e.target.form[0].value = title;
    e.target.form[1].value = body;
    e.target.form.parentNode.style.display = "none";
    edit(id);
  };
  const [curPage, setCurPage] = useState(1);
  const [itemsLoaded] = useState(props.state.ui.itemsLoaded);
  const lastItemIndex = curPage * itemsLoaded;
  const firstItemIndex = 0;
  const lastPageIndex = Math.ceil(props.state.articles.length / itemsLoaded);
  const currentItems = props.state.articles.slice(
    firstItemIndex,
    lastItemIndex
  );
  const showMoreOnScroll = (e) => {
    let clientH = e.target.scrollingElement.clientHeight;
    let scrollH = e.target.body.scrollHeight;
    let scrollT = e.target.scrollingElement.scrollTop;
    if (+scrollH - +clientH <= +scrollT) {
      if (curPage !== lastPageIndex) {
        setCurPage(curPage + 1);
      }
    }
  };
  document.addEventListener("scroll", showMoreOnScroll);
  const showMore = (e) => {
    if (curPage !== lastPageIndex) {
      setCurPage(curPage + 1);
    }
  };
  return (
    <div>
      <ArticleList
        currentItems={currentItems}
        data={returnData()}
        toggleBigCards={toggleBigCards}
        deleteArticle={deleteArticle}
        addArticle={addArticle}
        handleView={handleView}
        view={view}
        aId={articleData}
        changeColor={changeColor}
        handleEdit={handleEdit}
        handleChangeArticle={handleChangeArticle}
        edit={edit}
        showMore={showMore}
        curPage={curPage}
        lastPageIndex={lastPageIndex}
      />
    </div>
  );
}
function mapStateToProps(state) {
  return { state };
}
function mapDispatchToProps(dispatch) {
  return {
    addArticle: (id, userId, title, body) => {
      dispatch(addArticle(id, userId, title, body));
    },
    deleteArticle: (id) => {
      dispatch(deleteArticle(id));
    },
    toggleBigCards: () => {
      dispatch(toggleBigCards());
    },
    handleChangeColor: (id) => {
      dispatch(handleChangeColor(id));
    },
    handleChangeArticle: (id, title, body) => {
      dispatch(handleChangeArticle(id, title, body));
    },
    handleView: (id) => {
      dispatch(handleView(id));
    },
    handleEdit: (id) => {
      dispatch(handleEdit(id));
    }
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Articles);
