import { memo } from "react";
import { FormWrap, Form, Close } from "../styled-components/styles";
function ViewForm({ data, handleView }) {
  const close = (e) => {
    handleView(data.id);
    e.target.parentNode.parentNode.style.display = "none";
  };
  return (
    <FormWrap id="viewForm" style={{ display: "none" }}>
      <Form>
        <Close onClick={close}>X</Close>
        {data ? (
          <>
            <div className="view-title">{data.title}</div>
            <div className="view-body">{data.body}</div>
          </>
        ) : (
          ""
        )}
      </Form>
    </FormWrap>
  );
}
export default memo(ViewForm);
