import initialState from "./initialState";

export default function articleReducer(
  state = initialState.article.articles,
  action
) {
  switch (action.type) {
    case "ADD_ARTICLE": {
      return [
        ...state,
        {
          id: action.payload.id,
          userId: action.payload.userId,
          title: action.payload.title,
          body: action.payload.body,
          view: false,
          changeColor: 1,
          edit: false
        }
      ];
    }
    case "DELETE_ARTICLE": {
      const id = action.payload.id;
      return [...state.filter((item) => +item.id !== +id)];
    }
    case "HANDLE_VIEW": {
      const id = action.payload.id;
      return [
        ...state.map((item) => {
          if (+item.id === +id) {
            item.view = !item.view;
            return item;
          } else {
            return item;
          }
        })
      ];
    }
    case "HANDLE_EDIT": {
      const id = action.payload.id;
      return [
        ...state.map((item) => {
          if (+item.id === +id) {
            item.edit = !item.edit;
            return item;
          } else {
            return item;
          }
        })
      ];
    }
    case "HANDLE_CHANGE_ARTICLE": {
      const id = action.payload.id;
      return [
        ...state.map((item) => {
          if (+item.id === +id) {
            item.title = action.payload.title;
            item.body = action.payload.body;
            return item;
          } else {
            return item;
          }
        })
      ];
    }
    case "HANDLE_CHANGE_COLOR": {
      const id = action.payload.id;
      return [
        ...state.map((item) => {
          if (+item.id === +id) {
            if (item.changeColor < 3) {
              item.changeColor += 1;
            } else if (item.changeColor === 3) {
              item.changeColor = 1;
            }
            return item;
          } else {
            return item;
          }
        })
      ];
    }
    default: {
      return state;
    }
  }
}
