export const addArticle = (id, userId = 0, title, body) => {
  return {
    type: "ADD_ARTICLE",
    payload: {
      id,
      userId,
      title,
      body
    }
  };
};
export const deleteArticle = (id) => {
  return {
    type: "DELETE_ARTICLE",
    payload: {
      id
    }
  };
};
export const toggleBigCards = () => {
  return {
    type: "TOGGLE_BIG_CARDS"
  };
};
export const handleEdit = (id) => {
  return {
    type: "HANDLE_EDIT",
    payload: {
      id
    }
  };
};
export const handleView = (id) => {
  return {
    type: "HANDLE_VIEW",
    payload: {
      id
    }
  };
};
export const handleChangeArticle = (id, title, body) => {
  return {
    type: "HANDLE_CHANGE_ARTICLE",
    payload: {
      id,
      title,
      body
    }
  };
};
export const handleChangeColor = (id) => {
  return {
    type: "HANDLE_CHANGE_COLOR",
    payload: {
      id
    }
  };
};
