import { memo } from "react";
import { FormWrap, Form, FormTitle, Close } from "../styled-components/styles";
function AddForm({ addArticle, closeForm }) {
  return (
    <FormWrap style={{ display: "none" }}>
      <Form id="addForm">
        <Close onClick={closeForm}>X</Close>
        <FormTitle>Add Article</FormTitle>
        <div className="add-form-body">
          <input type="text" className="add-form-title" placeholder="Title" />
          <textarea type="text" className="add-form-body" placeholder="Body" />
          <input
            type="submit"
            className="add-form-submit"
            onClick={addArticle}
          />
        </div>
      </Form>
    </FormWrap>
  );
}
export default memo(AddForm);
